"use client";

import React from "react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { ArrowLeft, ArrowRight, Calendar, Clock, User, Moon, Sun } from "lucide-react";
import { Card, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import Navigation from "@/app/components/Navigation";
import { useTheme } from "@/app/contexts/ThemeContext";

// --- Types ---
type RelatedPost = {
  slug: string;
  title: string;
  excerpt: string;
};

type Post = {
  category: string;
  title: string;
  date: string;
  readTime: string;
  author: string;
  image: string;
  content: React.ReactNode;
  relatedPosts: RelatedPost[];
};

type BlogData = Record<string, Post>;

interface BlogPostPageProps {
  params: { slug: string };
}

// --- Metadata ---
export const metadata = {
  title: "Blog Post | Tech & Software Insights",
  description: "Read our latest articles on software development, AI, cloud computing, cybersecurity, and emerging technologies.",
};

// --- Static Params for SSG ---
export function generateStaticParams() {
  return [
    { slug: "latest-ai-trends-2025" },
    { slug: "cloud-computing-best-practices" },
    { slug: "cybersecurity-2025-guide" },
    { slug: "top-programming-languages-2025" },
    { slug: "blockchain-and-web3" },
    { slug: "emerging-iot-technologies" },
  ];
}

// --- Component ---
export default function BlogPostPage({ params }: BlogPostPageProps) {
  const { slug } = params;
  const { theme, toggleTheme } = useTheme();

  // --- Blog Data ---
  const blogData: BlogData = {
    "latest-ai-trends-2025": {
      category: "AI & Machine Learning",
      title: "Latest AI Trends in 2025: What to Expect",
      date: "2025-12-15",
      readTime: "6 min read",
      author: "Mahbub",
      image: "/ai.png",
      content: (
        <>
          <p className="text-xl text-slate-600 dark:text-slate-300 leading-relaxed mb-6">
            Artificial Intelligence continues to evolve rapidly. In 2025, AI is transforming industries through generative models,
            automation, and advanced machine learning applications.
          </p>
          <h2 className="text-3xl font-bold text-slate-900 dark:text-slate-100 mt-12 mb-4">Generative AI</h2>
          <p className="text-slate-700 dark:text-slate-300 leading-relaxed mb-6">
            Generative AI tools are enabling developers and businesses to create realistic images, text, and code automatically,
            enhancing productivity and creativity.
          </p>
          <h2 className="text-3xl font-bold text-slate-900 dark:text-slate-100 mt-12 mb-4">AI in Business Automation</h2>
          <p className="text-slate-700 dark:text-slate-300 leading-relaxed mb-6">
            AI-powered automation is streamlining workflows in finance, healthcare, and IT operations, reducing manual work and
            improving decision-making.
          </p>
          <div className="bg-indigo-50 dark:bg-indigo-900 border-l-4 border-indigo-600 p-6 my-8">
            <h3 className="text-xl font-bold text-slate-900 dark:text-slate-100 mb-2">Expert Tip</h3>
            <p className="text-slate-700 dark:text-slate-300">
              Staying updated with AI research and industry applications is key for developers and tech professionals.
            </p>
          </div>
        </>
      ),
      relatedPosts: [
        { slug: "cloud-computing-best-practices", title: "Cloud Computing Best Practices", excerpt: "Optimize performance and security." },
        { slug: "top-programming-languages-2025", title: "Top Languages in 2025", excerpt: "Languages driving the AI revolution." },
      ],
    },
    // Add all other blog data similarly...
  };

  const post: Post | undefined = blogData[slug];
  if (!post) return <p className="text-center py-20 text-slate-800 dark:text-slate-200">Post not found.</p>;

  return (
    <div className={`flex flex-col min-h-screen bg-white dark:bg-gray-900 transition-colors duration-500`}>
      {/* Navigation */}
      <Navigation />

      {/* Theme Toggle */}
      <div className="fixed bottom-6 right-6 z-50">
        <Button onClick={toggleTheme} variant="outline">
          {theme === "dark" ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
        </Button>
      </div>

      {/* Back Button */}
      <section className="bg-gradient-to-br from-sky-50 via-white to-indigo-50 dark:from-gray-800 dark:via-gray-900 dark:to-gray-800 py-12">
        <div className="container mx-auto px-4">
          <Button asChild variant="ghost" className="mb-6">
            <Link href="/blog">
              <ArrowLeft className="w-4 h-4 mr-2" />
              Back to Blog
            </Link>
          </Button>
        </div>
      </section>

      {/* Blog Content */}
      <article className="py-12">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto">
            <div className="mb-8">
              <span className="text-sm font-semibold px-3 py-1 bg-indigo-100 dark:bg-indigo-800 text-indigo-800 dark:text-indigo-100 rounded-full">
                {post.category}
              </span>
            </div>

            <h1 className="text-4xl md:text-5xl font-bold text-slate-900 dark:text-slate-100 mb-6">{post.title}</h1>

            <div className="flex flex-wrap items-center gap-6 text-slate-600 dark:text-slate-300 mb-8">
              <div className="flex items-center gap-2">
                <Calendar className="w-5 h-5" />
                <span>{new Date(post.date).toLocaleDateString()}</span>
              </div>
              <div className="flex items-center gap-2">
                <Clock className="w-5 h-5" />
                <span>{post.readTime}</span>
              </div>
              <div className="flex items-center gap-2">
                <User className="w-5 h-5" />
                <span>{post.author}</span>
              </div>
            </div>

            <div className="aspect-video overflow-hidden rounded-2xl mb-12">
              <img src={post.image} alt={post.title} className="w-full h-full object-cover" />
            </div>

            <div className="prose prose-lg max-w-none text-slate-800 dark:text-slate-200">{post.content}</div>

            <div className="mt-12 pt-8 border-t border-slate-200 dark:border-slate-700">
              <Button asChild size="lg" className="bg-indigo-600 hover:bg-indigo-700">
                <a href="https://yourportfolio.com/contact" target="_blank" rel="noopener noreferrer">
                  Contact Us
                  <ArrowRight className="ml-2 w-5 h-5" />
                </a>
              </Button>
            </div>
          </div>
        </div>
      </article>

      {/* Related Posts */}
      <section className="py-20 bg-slate-50 dark:bg-gray-800">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto">
            <h2 className="text-3xl font-bold text-slate-900 dark:text-slate-100 mb-8">Related Articles</h2>
            <div className="grid md:grid-cols-2 gap-6">
              {post.relatedPosts.map((related: RelatedPost) => (
                <Link key={related.slug} href={`/blog/${related.slug}`}>
                  <Card className="hover:shadow-lg transition-shadow dark:bg-gray-900 dark:border-gray-700">
                    <CardHeader>
                      <CardTitle className="hover:text-indigo-600 transition-colors">{related.title}</CardTitle>
                      <CardDescription>{related.excerpt}</CardDescription>
                      <div className="mt-2 text-indigo-600 font-medium inline-flex items-center gap-1">
                        Read More Blogs
                        <ArrowRight className="w-4 h-4" />
                      </div>
                    </CardHeader>
                  </Card>
                </Link>
              ))}
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
